.video__player {
  display: flex;
  align-items: center;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    margin-right: 60px;
    margin-left: 60px;
  }
  @media (min-width: $desktop-width) {
    margin-right: 200px;
    margin-left: 200px;
  }
}

.video__picture {
  margin-bottom: -45px;
  @media (min-width: $tablet-width) {
   margin-bottom: -93px;
  }
}

.video__controls {
  display: grid;
  grid-template-rows: 45px 66px;
  grid-template-columns: 70px 140px 70px;
  grid-template-areas:
          "scale scale scale"
          "replay subtitles fullscreen";
  background-color: rgba($black, 0.6);
  margin-right: 20px;
  margin-left: 20px;

  @media (min-width: $tablet-width) {
    min-width: 648px;
    margin-right: 0px;
    margin-left: 0px;
    grid-template-rows: 93px;
    grid-template-columns: 70px 1fr 70px 70px;
    grid-template-areas:
            "replay scale subtitles fullscreen";
  }
  @media (min-width: $desktop-width) {
    min-width: 800px;
  }
}
